<template>
    <div id="app">
        <button v-if="!isStarted" @click="start">START</button>
        <div class="mapContainer">
            <canvas ref="canvas" class="canvas"></canvas>
            <div ref="hostHUD" class="hostHUD">
                <div>Сегмент: <strong class="segmentName"></strong></div>
                <div>Хост: <strong class="hostName"></strong></div>
                <div>IP хоста: <strong class="hostIP"></strong></div>
            </div>
        </div>
    </div>
</template>

<script>
    import { start } from './interactiveMap';

    export default {
        name: 'App',
        data: () => {
            return {
                isStarted: false,
            };
        },
        mounted() {
            this.isStarted = true;
            start(this.canvas,this.hostHUD);
        },
        methods: {
            start() {
                this.isStarted = true;
                start(this.canvas, this.hostHUD);
            },
        },
        computed: {
            canvas() {
                return this.$refs.canvas;
            },
            hostHUD() {
                return this.$refs.hostHUD;
            },
        },
    };
</script>
<style>
    body {
        margin: 0;
        padding: 0;
    }
</style>
<style scoped>
    .hostHUD {
        width: 200px;
        height: 200px;
        border: 1px solid #000;
        position: absolute;
        right: 0;
        bottom: 0;
        background: #fff;
        padding: 5px;
    }

    .mapContainer {
        width: calc(100vw - 20px);
        height: calc(100vh - 20px);
        position: relative;
    }
</style>
