
import routerTexture from "../textures/router.png"
import {
    DoubleSide,
    Mesh,
    Group,
    BoxGeometry,
    MeshStandardMaterial,
    TextureLoader,
    RepeatWrapping,
    Vector2,
    NearestFilter,
} from 'three';

const textureLoader = new TextureLoader();

export class Router {
    #id;
    #instance;

    constructor(id, x, z) {
        this.#id = id;
        this.create(x, z);
    }

    create(x, z) {
        const geometry = new BoxGeometry(3, 3, 0.1);
        const texture = textureLoader.load(routerTexture);
        texture.rotation = -0.8
        texture.offset = new Vector2(0.5,-0)
        texture.wrapS = RepeatWrapping;
        texture.wrapT = RepeatWrapping;
        texture.magFilter = NearestFilter;
        // texture.needsUpdate = true;
        texture.repeat.x = 0.7;
        texture.repeat.y = 0.7;
        const material = new MeshStandardMaterial({
            // color: 0xffdd00,
            side: DoubleSide,
            map: texture
        });
        const plane = new Mesh(geometry, material);
        plane.position.set(x, 0.01, z);
        plane.rotation.x = Math.PI / 2;
        plane.rotation.z = Math.PI / 4;
        const planeGroup = new Group();
        planeGroup.add(plane);
        this.#instance = planeGroup;
    }

    get instance() {
        return this.#instance;
    }
}
