import hostImage from '../textures/host.jpg';
import { TextureLoader } from 'three';
import { InteractiveMap } from './InteractiveMap';
const textureLoader = new TextureLoader();

function getRandomInt(max) {
    return Math.floor(Math.random() * max);
  }

export function start(canvas, hostHUD) {
    const rendererSizes = {
        width: window.innerWidth - 20,
        height: window.innerHeight - 20,
    };
    const lightPositions = { x: 10, y: 10, z: -4 };
    const IM = new InteractiveMap(
        rendererSizes,
        lightPositions,
        canvas,
        hostHUD
    );

    const internetSegment = IM.createSegment('Internet', -9, -3, 'Internet');
    const DMZSegment = IM.createSegment('DMZ', -3, -3, 'DMZ');
    const DataCenterSegment = IM.createSegment(
        'Data Center',
        3,
        -3,
        'Data Center'
    );
    const OfficeUsersSegment = IM.createSegment(
        'Office users',
        9,
        -3,
        'Office users'
    );
    const ASUSegment = IM.createSegment('ASU', 15, -3, 'ASU');

    const segments = [
        internetSegment,
        DMZSegment,
        DataCenterSegment,
        OfficeUsersSegment,
        ASUSegment,
    ];

    textureLoader.load(hostImage, (texture) => {
        texture.repeat.x = 0.4;
        texture.repeat.y = 0.4;
        texture.offset.set(0.5, 0.5);
        internetSegment.createHost(
            texture,
            'Kali',
            -1.5,
            -1.5,
            'Kali',
            '185.88.181.55'
        );
        internetSegment.createHost(
            texture,
            'ProxyChecker',
            0,
            -1.5,
            'Proxy\nChecker',
            '185.88.181.88'
        );

        DMZSegment.createHost(
            texture,
            'CMS Drupal',
            -1.5,
            -1.5,
            'CMS\n Drupal',
            '10.10.1.20'
        );
        DMZSegment.createHost(
            texture,
            'Web Server 2',
            0,
            -1.5,
            'Web\n Server 2',
            '10.10.1.21'
        );
        DMZSegment.createHost(
            texture,
            'Appache Tomcat',
            1.5,
            -1.5,
            'Appache\n Tomcat',
            '10.10.1.24'
        );
        DMZSegment.createHost(
            texture,
            'Security Onion',
            -1.5,
            0,
            'Security\n Onion',
            '10.10.1.25'
        );

        DataCenterSegment.createHost(
            texture,
            'MS Active Directory',
            -1.5,
            -1.5,
            'MS Active \nDirectory',
            '10.10.2.10'
        );
        DataCenterSegment.createHost(
            texture,
            'MS Exchange',
            0,
            -1.5,
            'MS \nExchange',
            '10.10.2.11'
        );
        DataCenterSegment.createHost(
            texture,
            'MS FileServer',
            1.5,
            -1.5,
            'MS \nFileServer',
            '10.10.2.12'
        );
        DataCenterSegment.createHost(
            texture,
            'Database Server',
            -1.5,
            -0.5,
            'Database \nServer',
            '10.10.2.13'
        );
        DataCenterSegment.createHost(
            texture,
            'SSH Server',
            0,
            -0.5,
            'SSH \nServer',
            '10.10.2.14'
        );
        DataCenterSegment.createHost(
            texture,
            'Redmine Server',
            1.5,
            -0.5,
            'Redmine \nServer',
            '10.10.2.15'
        );
        DataCenterSegment.createHost(
            texture,
            'CMS Made Simple',
            -1.5,
            0.5,
            'CMS Made \nSimple',
            '10.10.2.16'
        );
        DataCenterSegment.createHost(
            texture,
            'ViPNet IDS HS',
            0,
            0.5,
            'ViPNet \nIDS HS',
            '10.10.2.17'
        );
        DataCenterSegment.createHost(
            texture,
            'ViPNet IDS NS',
            1.5,
            0.5,
            'ViPNet \nIDS NS',
            '10.10.2.21'
        );
        DataCenterSegment.createHost(
            texture,
            'ViPNet TIAS',
            1.5,
            1.5,
            'ViPNet \nTIAS',
            '10.10.2.23'
        );

        OfficeUsersSegment.createHost(
            texture,
            'Administrator Workstation',
            -1.5,
            -1.5,
            'Administrator\n Workstation',
            '10.10.4.10'
        );
        OfficeUsersSegment.createHost(
            texture,
            'Manager Workstation 1',
            0,
            -1.5,
            'Manager\n Workstation 1',
            '10.10.4.11'
        );
        OfficeUsersSegment.createHost(
            texture,
            'Response Team Workstation',
            1.5,
            -1.5,
            'Response\n Team Workstation',
            '10.10.4.12'
        );
        OfficeUsersSegment.createHost(
            texture,
            'Insider Workstation',
            -1.5,
            0,
            'Insider Workstation',
            '10.10.4.17'
        );
        OfficeUsersSegment.createHost(
            texture,
            'Developer Workstation',
            0,
            0,
            'Developer\n Workstation',
            '10.10.4.13'
        );
        OfficeUsersSegment.createHost(
            texture,
            'Manager Workstation 2',
            1.5,
            0,
            'Manager\n Workstation 2',
            '10.10.4.14'
        );

        ASUSegment.createHost(
            texture,
            'SCADA IGSS',
            -1.5,
            -1.5,
            'SCADA \nIGSS',
            '10.10.3.10'
        );

        const router1 = IM.createRouter('router1', -6, 2);
        const router2 = IM.createRouter('router2', 0, 2);

        setInterval(() => {
            const randomSegment = getRandomInt(segments.length - 1)
            const segment = segments[randomSegment];
            const randomHost = getRandomInt(segments.length - 1)
            segment.hosts[randomHost]?.generateEvent()
        }, 2000)
    });
}
