import segmentTexture from '../textures/segment.png';
import {
    BoxGeometry,
    Color,
    DoubleSide,
    Group,
    Mesh,
    MeshLambertMaterial,
    MeshStandardMaterial,
    TextureLoader,
} from 'three';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { SEGMENT_WIDTH, SEGMENT_HEIGHT, SEGMENT_DEPTH } from './const';
import { Host } from './Host';

const textureLoader = new TextureLoader();
const fontLoader = new FontLoader();

export class Segment {
    #id;
    #hosts= [];
    #textInstance;
    #instance;
    #name;
    #interactiveMap;

    constructor(id, x, z, text, interactiveMap) {
        this.#id = id;
        this.#name = text;
        this.#instance = this.create(id, x, z);
        this.#interactiveMap = interactiveMap;
        this.createText(text);
    }

    create(id, x, z) {
        const texture = textureLoader.load(segmentTexture)
        const segmentMaterial = new MeshStandardMaterial({
            name: `segment_${id}`,
            map: texture
        });
        const segment = new BoxGeometry(
            SEGMENT_WIDTH,
            SEGMENT_HEIGHT,
            SEGMENT_DEPTH
        );
        const segmentMesh = new Mesh(segment, segmentMaterial);
        segmentMesh.rotation.x = -1.57;
        segmentMesh.castShadow = true;
        const segmentGroup = new Group();

        segmentGroup.add(segmentMesh);

        segmentGroup.position.set(x, SEGMENT_DEPTH / 2, z);
        return segmentGroup;
    }

    createText(text) {
        fontLoader.load(
            'https://cdn.rawgit.com/mrdoob/three.js/master/examples/fonts/helvetiker_regular.typeface.json',
            (font) => {
                const geometry = new TextGeometry(text, {
                    font: font,
                    size: 40,
                    height: 4,
                    curveSegments: 1,
                    bevelEnabled: true,
                    bevelThickness: 1,
                    bevelSize: 1,
                    bevelSegments: 5,
                });

                let material = new MeshLambertMaterial({
                    color: 0xf3ffe2,
                    name: 'text',
                    side: DoubleSide,
                });

                const textMesh = new Mesh(geometry, material);
                textMesh.position.set(-1.8, 0, 1.8);
                textMesh.rotation.x = -Math.PI / 2;
                textMesh.scale.multiplyScalar(0.01);

                this.#instance.add(textMesh);
                this.#textInstance = textMesh;
            }
        );
    }

    createHost(texture, id, x, z, text, ip) {
        const host = new Host(texture, this, id, x, z, text, ip);
        this.#instance.add(host.instance);
        this.#hosts.push(host);
        return host;
    }

    get instance() {
        return this.#instance;
    }

    get name(){
        return this.#name;
    }

    get interactiveMap(){
        return this.#interactiveMap;
    }

    get hosts(){
        return this.#hosts;
    }
}
