import {
    BoxGeometry,
    Group,
    Mesh,
    MeshLambertMaterial,
    TextureLoader,
    MeshStandardMaterial,
    MeshBasicMaterial,
    Color,
    SphereGeometry,
} from 'three';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { HOST_WIDTH, HOST_HEIGHT, HOST_DEPTH, SEGMENT_DEPTH } from './const';
const fontLoader = new FontLoader();

export class Host {
    #instance;
    #textInstance;
    #segment;
    #texture;
    #modal;
    #name;
    #ip;
    #animateEvents = new Set();
    #animateEventsCount = 0;

    constructor(
        texture,
        segment,
        name,
        x,
        z,
        text,
        ip
    ) {
        this.#name = name;
        this.#ip = ip;
        this.#texture = texture;
        this.#segment = segment;
        this.#modal = this.createModal();
        this.#instance = this.create(name, x, z);
        this.createText(text);
    }

    create(name, x, z) {
        const hostMaterial = new MeshStandardMaterial({
            name: `host_${name}`,
            map: this.#texture,
        });

        const host = new BoxGeometry(
            HOST_WIDTH,
            HOST_HEIGHT,
            HOST_DEPTH
        );

        const hostMesh = new Mesh(host, hostMaterial);
        hostMesh.castShadow = true;
        hostMesh.name = 'host';
        hostMesh.userData._modal = this.#modal
        hostMesh.userData.segment = this.#segment
        hostMesh.userData.host = this
        
        const hostGroup = new Group();
        hostGroup.add(hostMesh);
        
        this.#segment.instance.add(hostGroup);
        hostGroup.position.set(x, HOST_DEPTH + SEGMENT_DEPTH, z);
        return hostGroup;
    }

    createText(text) {
        fontLoader.load(
            'https://cdn.rawgit.com/mrdoob/three.js/master/examples/fonts/helvetiker_regular.typeface.json',
            (font) => {
                const geometry = new TextGeometry(text, {
                    font: font,
                    size: 12,
                    height: 5,
                    curveSegments: 1,
                    bevelEnabled: true,
                    bevelThickness: 1,
                    bevelSize: 1,
                    bevelSegments: 1,
                });

                let material = new MeshLambertMaterial({
                    color: 0xf3ffe2,
                    name: 'text',
                });

                const textMesh = new Mesh(geometry, material);
                textMesh.name = 'hostText';
                textMesh.position.set(0.2, 0.4, 0);
                textMesh.rotation.x = -Math.PI / 2;
                textMesh.rotation.y = Math.PI / 2;
                textMesh.scale.multiplyScalar(0.01);
                this.#textInstance = textMesh;
                this.#instance.add(textMesh);
            }
        );
    }

    createModal(){
        const modalGeometry = new BoxGeometry(4, 8, 0.1);
        const modalMaterial = new MeshBasicMaterial({
            color: new Color(0,255,0),
        });
        const modalMesh = new Mesh(modalGeometry, modalMaterial);
        modalMesh.rotation.y = Math.PI / 2;

        const modalGroup = new Group()
        modalGroup.add(modalMesh)

        fontLoader.load(
            'https://cdn.rawgit.com/mrdoob/three.js/master/examples/fonts/helvetiker_regular.typeface.json',
            (font) => {
                const geometry= new TextGeometry('test', {
                    font: font,
                    size: 12,
                    height: 5,
                    curveSegments: 1,
                    bevelEnabled: true,
                    bevelThickness: 1,
                    bevelSize: 1,
                    bevelSegments: 1,
                });

                let material = new MeshLambertMaterial({
                    // color: 0xf3ffe2,
                    color: 0x000000,
                    name: 'text',
                });

                const textMesh = new Mesh(geometry, material);
                textMesh.position.set(0,0,0);
                textMesh.rotation.x = -Math.PI / 2;
                textMesh.rotation.y = Math.PI / 2;
                textMesh.scale.multiplyScalar(0.01);
                // modalGroup.add(textMesh)
            },
        );

        return modalGroup
    }

    generateEvent(){
        const sphereGeometry = new SphereGeometry(0.1, 64, 32);
        const sphereMaterial = new MeshBasicMaterial({
            color: 0xffff00,
            name: 'sphere',
        });
        const sphere = new Mesh(sphereGeometry, sphereMaterial);
        this.#instance.add(sphere)
        this.addAnimateEvent(this.animateEvent, sphere)
    }

    addAnimateEvent(fn, ...args){
        const eventId = `host_${this.#name}_Event_${this.#animateEventsCount}`
        this.#animateEventsCount += 1;
        this.#animateEvents.add(eventId)
        this.interactiveMap.elementsForAnimated[eventId] = fn.bind(this, eventId, ...args)()
    }

    removeAnimateEvent(eventId){
        this.#animateEvents.delete(eventId)
        delete this.interactiveMap.elementsForAnimated[eventId]
    }

    animateEvent(eventId, sphere){
        let direction = "up"
        const host = this;
        return () => {
            if (direction == "up"){
                sphere.position.y += 0.1;
                if (sphere.position.y >= 0.7){
                    direction = "down"
                }
            } else if (direction == "down"){
                sphere.position.y -= 0.01;
                if (sphere.position.y <= 0){
                    direction = "up"
                    host.removeAnimateEvent(eventId)
                }
            }
        }
    }

    get interactiveMap(){
        return this.#segment.interactiveMap;
    }
    

    get instance() {
        return this.#instance;
    }

    get name(){
        return this.#name;
    }

    get ip(){
        return this.#ip;
    }

}
